import { Container, Row, Col } from "react-bootstrap"
import "animate.css"
import { Canvas } from "react-three-fiber"
import SpaceScene from "./SpaceScene"
import TrackVisibility from "react-on-screen"

export const Banner = () => {
  return (
    <>
      <Canvas
        style={{
          position: "absolute",
          width: "100%",
          height: "50%",
          pointerEvents: "none",
        }}
      >
        <SpaceScene />
      </Canvas>
      <section className="banner" id="home">
        <Container>
          <Row>
            <Col xs={12} md={6} xl={7}>
              <span className="tagline">Welcome to my Portfolio</span>

              <h1>{`Konstantinos Gkagiannis`}</h1>

              <p>
                I hold a Master's degree in Computer Engineering and
                Informatics, specializing in various programming languages. My
                experience as a Front End Developer has provided me with a solid
                foundation, and hands-on work has enhanced my ability to adapt
                and innovate. I am enthusiastic about using technology to tackle
                real-world challenges.
              </p>
            </Col>
            <Col xs={12} md={6} xl={5}></Col>
          </Row>
        </Container>
      </section>
    </>
  )
}
